import React,{useState,useEffect} from 'react'
import {Line} from "react-chartjs-2";

const LineGraph = ({id, data,label,unit,color,months, is_all,yillar}) => {
    const [chartData,setChartData] = useState({})
    const chart =  ()=>{

        setChartData({
            labels: convertMonths(months),
            datasets: [
              {
                label: label +" - "+ unit,
                data:  data,
                fill: false,
                backgroundColor:  "rgba(103, 155, 163, 1)",
                borderColor: 'rgba(50, 150, 250, 0.8)',
              },
            ],
        })

    }


    useEffect(()=>{
        chart()

    },[])
    const convertMonths = (months) =>{
        const labels = []
        months.map( (month, index) => {
           switch (month) {
               case "01":
                   labels.push(`${is_all ? yillar[index]: "" } Ocak`)
                   break;
                case "02":
                    labels.push(`${is_all ? yillar[index]: "" } Åubat`)
                    break;
                case "03":
                    labels.push(`${is_all ? yillar[index]: "" } Mart`)
                    break;
                case "04":
                    labels.push(`${is_all ? yillar[index]: "" } Nisan`)
                    break;
                case "05":
                    labels.push(`${is_all ? yillar[index]: "" } MayÄ±s`)
                    break;
                case "06":
                    labels.push(`${is_all ? yillar[index]: "" } Haziran`)
                    break;
                case "07":
                    labels.push(`${is_all ? yillar[index]: "" } Temmuz`)
                    break;
                case "08":
                    labels.push(`${is_all ? yillar[index]: "" } AÄutos`)
                    break;
                case "09":
                    labels.push(`${is_all ? yillar[index]: "" } EylÃ¼l`)
                    break;
                case "10":
                    labels.push(`${is_all ? yillar[index]: "" } Ekim`)
                    break;
                case "11":
                    labels.push(`${is_all ? yillar[index]: "" } KasÄ±m`)
                    break;
                case "12":
                    labels.push(`${is_all ? yillar[index]: "" } AralÄ±k`)
                    break;
               default:
                   break;
           }
       })
       return labels;

    }
    return (
        <>
              <Line id={id} data={chartData}
                          options={{
                              maintainAspectRatio: false,
                              scales: {
                                  yAxes: [{
                                      stacked: true,
                                      gridLines: {
                                          display: true,
                                          color: "rgba(0,0,0,0.1)"
                                      },
                                      scaleLabel: {
                                          display: true,
                                          labelString: unit,
                                          fontSize: 18
                                      },
                                      ticks:{

                                          stepSize: 5
                                      }

                                  }],
                                  xAxes: [{
                                      gridLines: {
                                          display: true
                                      } ,
                                      ticks:{
                                        fontSize:14
                                    },

                                  }],
                                  yAxes: [{
                                    gridLines: {
                                        display: true
                                    } ,
                                    ticks:{
                                      min: Math.min(...data) - Math.min(...data) * 0.2,
                                      max:Math.max(...data) + Math.max(...data) * 0.2,
                                      fontSize:14
                                  },

                                }]
                              },



                          }}/>
        </>
    )
}

export default LineGraph
